<template lang="html">
  <div class="home">
    <h1>MY Recipes</h1>
    <button type="button" @click="togglepopup">Add New Recipe</button>
    <div class="row">
      <div class="col-lg-12">
        <div class="recipes">
          <div
            class="card"
            v-for="recipe in $store.state.recipes"
            :key="recipe.slug"
          >
            <div class="card-body">
              <h2 class="card-title">{{ recipe.Title }}</h2>
              <p class="card-text">{{ recipe.description }}</p>
              <router-link :to="`/recipe/${recipe.slug}`">
                <button>View Recipe</button>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="add-recipe-popup" v-if="popupopen">
      <div class="popup-content">
        <h2>Add New Recipe</h2>
        <form @submit.Prevent="addnewRecipe">
          <div class="group">
            <label>Title</label>
            <input type="text" v-model="newRecipe.Title" />
          </div>

          <div class="group">
            <label>Description</label>
            <textarea v-model="newRecipe.description"></textarea>
          </div>

          <div class="group">
            <label>Ingredient</label>
            <div
              class="ingredient"
              v-for="i in newRecipe.ingredientrows"
              :key="i"
            >
              <input type="text" v-model="newRecipe.ingredients[i - 1]" />
            </div>
            <button type="button" @click="addnewIngredient">
              Add Ingredient
            </button>
          </div>

          <div class="group">
            <label>method</label>
            <div class="method" v-for="i in newRecipe.methodRows" :key="i">
              <textarea v-model="newRecipe.method[i - 1]"></textarea>
            </div>
            <button type="button" @click="addnewStep">Add Step</button>
          </div>
          <button type="submit">Add Recipe</button>
          <button type="button" @click="togglepopup">Close</button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { ref } from "vue";
import { useStore } from "vuex";
export default {
  name: "Home",
  setup() {
    const newRecipe = ref({
      Title: "",
      description: "",
      ingredients: [],
      method: [],
      ingredientrows: 1,
      methodRows: 1,
    });
    const popupopen = ref(false);
    const store = useStore();

    const addnewIngredient = () => {
      newRecipe.value.ingredientrows++;
    };

    const addnewStep = () => {
      newRecipe.value.methodRows++;
    };

    const togglepopup = () => {
      popupopen.value = !popupopen.value;
    };

    const addnewRecipe = () => {
      newRecipe.value.slug = newRecipe.value.Title.toLowerCase().replace(
        /\s/g,
        "-"
      );
      if (newRecipe.value.slug == "") {
        alert("Please Enter A Title");
        return;
      }
      store.commit("ADD_Recipe", { ...newRecipe.value });

      newRecipe.value = {
        Title: "",
        description: "",
        ingredients: [],
        method: [],
        ingredientrows: 1,
        methodRows: 1,
      };

      togglepopup();
    };
    return {
      newRecipe,
      popupopen,
      togglepopup,
      addnewIngredient,
      addnewStep,
      addnewRecipe,
    };
  },
};
</script>
